import { useState } from "react";
import scrambleGenerator from "rubiks-cube-scramble";
const CUBE_SCRAMBLE_OPTIONS_DEFAULT = {array: false, turns: 21};

export function useScramble({turns=21}: {turns?: number}): [string, () => void, (scramble: string) => void] {
	const [ scramble, setScramble ] = useState(scrambleGenerator({array: false, turns: turns || CUBE_SCRAMBLE_OPTIONS_DEFAULT.turns}) as string);
	const refreshScramble = () => setScramble(scrambleGenerator({array: false, turns: turns || CUBE_SCRAMBLE_OPTIONS_DEFAULT.turns}) as string);
	return [scramble, refreshScramble, setScramble];
}